	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Soon with McCafé!","date":"02 Oct 2007","slug":"/2007-10-02-soon-with-mccafe/","tags":"Y2007"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2007/10/demnachst.jpg\" alt=\"demna&#776;chst.jpg\" border=\"0\" width=\"450\" height=\"636\" /></p>\n<p>The remodeling won't start for another 6 weeks, but our first in-store posters are on their way.</p>"},"next":{"fields":{"title":"Deutschland Weltmeister. Again.","date":"30 Sep 2007","slug":"/2007-09-30-deutschland-weltmeister-again/"}},"previous":{"fields":{"title":"Big Mac Pulled","date":"03 Oct 2007","slug":"/2007-10-03-big-mac-pulled/"}}},{"node":{"fields":{"title":"Deutschland Weltmeister. Again.","date":"30 Sep 2007","slug":"/2007-09-30-deutschland-weltmeister-again/","tags":"Y2007"},"html":"<p>It was worth mentioning the <a href=\"http://www.papascott.de/archives/2003/10/13/deutschland-weltmeister/\">first time</a> 4 years ago, and even more so today. <a href=\"http://sports.yahoo.com/sow/news;_ylt=Ah.TALJzgoNRNYrgW0dPpn4mw7YF?slug=ap-wwcup-germany-brazil&amp;prov=ap&amp;type=lgns\">Germany defeated Brazil 2-0</a> to win their second straight Women's World Cup. Germany did not give up a single goal in the tournament, and keeper Nadine Angerer stopped a penalty and a number of good shots to ensure the win.</p>"},"next":{"fields":{"title":"Adieu DB","date":"29 Sep 2007","slug":"/2007-09-29-adieu-db/"}},"previous":{"fields":{"title":"Soon with McCafé!","date":"02 Oct 2007","slug":"/2007-10-02-soon-with-mccafe/"}}},{"node":{"fields":{"title":"Adieu DB","date":"29 Sep 2007","slug":"/2007-09-29-adieu-db/","tags":"Y2007"},"html":"<p>PapaScott (the blog, not the man) took an unexpected <a href=\"http://flickr.ishavingamassage.com/\">massage</a> the past couple of days. My MySQL database seems to have disappeared from my hoster, just a couple of days after upgrading to WordPress 2.3. I didn't have time to track down the problem, I simply restored the last backup dump (which I mail to myself daily), which of course had character set issues (mixed latin1 and utf8 characters). But now we're running pure UTF-8 on MySQL 5.0, and everything should be fine until I have to restore from backup again.</p>"},"next":{"fields":{"title":"Bike Tour","date":"21 Sep 2007","slug":"/2007-09-21-bike-tour/"}},"previous":{"fields":{"title":"Deutschland Weltmeister. Again.","date":"30 Sep 2007","slug":"/2007-09-30-deutschland-weltmeister-again/"}}}],"pathPrefix":"","first":false,"last":false,"index":105,"pageCount":1101}}

